
import { defineComponent, ref } from 'vue'
import { QuickFetch } from '@/components/quick'

export default defineComponent({
    setup() {
        const qf = new QuickFetch();
        const img_front: any = ref('');
        let front_data = "";
        function loadFront(event: any) {
            if(event.target.files[0].size>3000000){
                alert("檔案過大 File size is too large");
            }else{
                const reader: any = new FileReader();
                img_front.value = URL.createObjectURL(event.target.files[0]);
                reader.onload = function () {
                    let base64String: string = reader.result.replace("data:", "").replace(/^.+,/, "");
                    front_data = 'data:image/png;base64, '+base64String;
                }
                reader.readAsDataURL(event.target.files[0]);
            }
        }
        function getImage(){
            qf.Url('nhc/image').Get().then((res: any) => {
                img_front.value = res.image1;
            });
        }
        getImage();
        function submitAll(){
            if(front_data.length==0){
                alert("請上傳證件正面");
            }else{
                qf.Dataset().Add('image1',front_data);
                qf.Url('nhc/file').Post(qf.GetDset()).then((res: any)=>{
                    if(res.message=='done'){
                        alert("已上傳 Done");
                    }else{
                        alert('上傳失敗，請洽詢體育組 Failed, Please contact Physical & Health Division');
                    }
                });
            }
        }
        return {
            img_front,
            loadFront,
            submitAll,
        }
    },
})
